/*--------------------------------------------------------------
# Normalize
--------------------------------------------------------------*/
* {
	position: relative;
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	font-family: sans-serif;
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
}

body {
	margin: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
	display: block;
}

audio,
canvas,
progress,
video {
	display: inline-block;
	vertical-align: baseline;
}

audio:not([controls]) {
	display: none;
	height: 0;
}

[hidden],
template {
	display: none;
}

a {
	background-color: transparent;
}

a:active,
a:hover {
	outline: 0;
}

abbr[title] {
	border-bottom: 1px dotted;
}

b,
strong {
	font-weight: bold;
}

dfn {
	font-style: italic;
}

h1 {
	font-size: 2em;
	margin: 0.67em 0;
}

mark {
	background: #ff0;
	color: #000;
}

small {
	font-size: 80%;
}

sub,
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}

sup {
	top: -0.5em;
}

sub {
	bottom: -0.25em;
}

img {
	border: 0;
	display: block;
}

svg:not(:root) {
	overflow: hidden;
}

figure {
	margin: 1em 40px;
}

hr {
	box-sizing: content-box;
	height: 0;
}

pre {
	overflow: auto;
}

code,
kbd,
pre,
samp {
	font-family: monospace, monospace;
	font-size: 1em;
}

button,
input,
optgroup,
select,
textarea {
	color: inherit;
	font: inherit;
	margin: 0;
}

button {
	overflow: visible;
}

button,
select {
	text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
	-webkit-appearance: button;
	cursor: pointer;
}

button[disabled],
html input[disabled] {
	cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
	border: 0;
	padding: 0;
}

input {
	line-height: normal;
}

input[type="checkbox"],
input[type="radio"] {
	box-sizing: border-box;
	padding: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
	height: auto;
}

input[type="search"] {
	-webkit-appearance: textfield;
	box-sizing: content-box;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;
}

fieldset {
	border: 1px solid #c0c0c0;
	margin: 0 2px;
	padding: 0.35em 0.625em 0.75em;
}

legend {
	border: 0;
	padding: 0;
}

textarea {
	overflow: auto;
}

optgroup {
	font-weight: bold;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

td,
th {
	padding: 0;
}

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;
}

.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	color: #21759b;
	display: block;
	font-size: 17.5px;
	font-size: 1.09375rem;
	font-weight: bold;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000;
	/* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
	outline: 0;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.grid {
	display: flex;
	justify-content: space-around;
}

.alignleft {
	display: inline;
	float: left;
	margin-right: 1.5em;
}

.alignright {
	display: inline;
	float: right;
	margin-left: 1.5em;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.aligncenter img {
	width: 100%;
}

.wp-caption.aligncenter,
.centered-image {
	max-width: 100vw;
	margin: 0 -1em 1.6em;
}

@media screen and (min-width: 37.5rem) {
	.wp-caption.aligncenter,
	.centered-image {
		margin: 0 -2em 1.6em;
	}
}

blockquote.alignleft,
blockquote.alignright,
blockquote.aligncenter {
	width: 50%;
	padding: 1em;
	margin: 0 1.5em 1.5em 0;
	background: none;
	z-index: 1;
}

blockquote.alignleft:before, blockquote.alignleft:after,
blockquote.alignright:before,
blockquote.alignright:after,
blockquote.aligncenter:before,
blockquote.aligncenter:after {
	border: none;
}

blockquote.alignleft p,
blockquote.alignright p,
blockquote.aligncenter p {
	margin-bottom: 0;
}

blockquote.alignleft em, blockquote.alignleft i,
blockquote.alignright em,
blockquote.alignright i,
blockquote.aligncenter em,
blockquote.aligncenter i {
	font-style: italic;
	font-weight: 400;
	color: inherit;
}

blockquote.alignleft strong,
blockquote.alignright strong,
blockquote.aligncenter strong {
	color: inherit;
}

blockquote.alignleft {
	padding-bottom: 0;
}

blockquote.alignleft:after {
	border-right: 2px solid #51CF66;
	border-top: 2px solid #51CF66;
	padding-bottom: 0;
	position: absolute;
	top: 0;
	right: 0;
	height: 50%;
	width: 100%;
}

blockquote.alignright {
	padding-top: 0;
}

blockquote.alignright:after {
	border-left: 2px solid #51CF66;
	border-bottom: 2px solid #51CF66;
	padding-top: 0;
	position: absolute;
	bottom: 0;
	left: 0;
	height: 50%;
	width: 100%;
}

blockquote.aligncenter {
	width: 100%;
	padding: 1.5em;
}

blockquote.aligncenter:before {
	border-right: 2px solid #51CF66;
	border-top: 2px solid #51CF66;
	padding-bottom: 0;
	position: absolute;
	top: 1.25em;
	right: 0;
	height: 50%;
	width: 50%;
}

blockquote.aligncenter:after {
	border-left: 2px solid #51CF66;
	border-bottom: 2px solid #51CF66;
	padding-top: 0;
	position: absolute;
	bottom: 0;
	left: 0;
	height: 50%;
	width: 50%;
}

blockquote.long-quote,
.long-quote blockquote {
	font-size: 1em;
	font-style: normal;
	/*background: none;*/
	transform: skewX(0deg);
	padding-left: 2em;
	padding-right: 2em;
	/*color: $color__gray;*/
}

blockquote.long-quote h1, blockquote.long-quote h2, blockquote.long-quote h3, blockquote.long-quote h4, blockquote.long-quote h5, blockquote.long-quote h6,
.long-quote blockquote h1,
.long-quote blockquote h2,
.long-quote blockquote h3,
.long-quote blockquote h4,
.long-quote blockquote h5,
.long-quote blockquote h6 {
	font-size: 1.5em;
	/*color: $color__gray;*/
}

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.comment-content:before,
.comment-content:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
	content: "";
	display: table;
	table-layout: fixed;
}

.clear:after,
.entry-content:after,
.comment-content:after,
.site-content:after,
.site-footer:after {
	clear: both;
}

/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea {
	color: #4d5256;
	font-family: "Spoqa Han Sans", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	font-size: 16px;
	font-size: 1rem;
	line-height: 1.7;
}

@media all and (min-width: 700px) {
	body,
	button,
	input,
	select,
	textarea {
		font-size: 20px;
		font-size: 1.25rem;
	}
}

h1, h2, h3, h4, h5, h6 {
	font-family: "Oswald", "Spoqa Han Sans", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	color: #363a3c;
	font-weight: 400;
	clear: both;
	margin: 1em 0 0.5em;
	line-height: 1.3;
}

h1 strong, h1 i, h1 em, h2 strong, h2 i, h2 em, h3 strong, h3 i, h3 em, h4 strong, h4 i, h4 em, h5 strong, h5 i, h5 em, h6 strong, h6 i, h6 em {
	font-weight: 400;
}

h1 {
	font-family: "Oswald";
	font-size: 3em;
}

h2 {
	font-family: "Oswald";
	font-size: 2em;
}

h3 {
	font-size: 1.8em;
}

h4 {
	font-size: 1.6em;
}

h5 {
	font-size: 1.6em;
	font-family: "Satisfy", cursive;
}

h6 {
	font-size: 1em;
	color: #878d91;
	text-transform: uppercase;
	letter-spacing: 2px;
}

p {
	margin-bottom: 1.5em;
}

dfn, cite, em, i {
	font-style: italic;
	font-weight: 300;
}

cite, q {
	font-family: "Satisfy", cursive;
	font-style: normal;
}

strong, dt {
	color: #363a3c;
}

blockquote {
	margin: 1em 0em;
	font-weight: 100;
	font-size: 24px;
	font-size: 1.5rem;
	background: #f8fafb;
	padding: 2.5em 1.5em 1.5em;
	-webkit-transform: skewX(14deg);
	transform: skewX(14deg);
	font-style: italic;
}

blockquote:before, blockquote:after {
	content: "";
	position: absolute;
	width: 160px;
	border-top: 2px solid gainsboro;
}

blockquote:before {
	margin-top: -1em;
}

blockquote h1, blockquote h2, blockquote h3, blockquote h4, blockquote h5, blockquote h6 {
	margin-top: 0;
}

blockquote p {
	margin-bottom: 1em;
	quotes: "“" "”" "‘" "’";
}

blockquote p:before {
	content: open-quote;
}

blockquote p:after {
	content: close-quote;
}

blockquote dfn, blockquote em, blockquote i {
	font-style: italic;
	font-weight: 400;
	color: #878d91;
}

blockquote cite, blockquote q {
	font-style: italic;
}

address {
	margin: 0 0 1.5em;
	font-weight: 300;
}

pre {
	background: #363a3c;
	font-family: "Noto Mono", Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 15px;
	font-size: 0.9375rem;
	color: #a9afb3;
	line-height: 1.5;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code, kbd, tt, var {
	font-family: "Noto Mono", Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 18.75px;
	font-size: 1.17188rem;
	background: #f8fafb;
	padding: 0.3em 0.5em;
}

var {
	font-weight: 300;
}

abbr, acronym {
	color: #878d91;
	border-bottom: 1px dotted #878d91;
	text-decoration: none;
	cursor: help;
	font-style: italic;
	font-weight: 300;
}

mark, ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 26px;
	font-size: 1.625rem;
}

blockquote, q {
	quotes: "" "";
}

blockquote:before, blockquote:after, q:before, q:after {
	content: "";
}

.cursive {
	font-family: "Satisfy", cursive;
	font-size: 26px;
	font-size: 1.625rem;
}

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
	color: #111;
}

select {
	border: 1px solid #ccc;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"] {
	padding: 3px;
}

textarea {
	padding-left: 3px;
	width: 100%;
}

/*--------------------------------------------------------------
# Buttons
--------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	background: transparent;
	font-size: 15px;
	font-size: 0.9375rem;
	line-height: 1;
	padding: .6em 1em .4em;
}

button:hover, button:active, button:focus,
input[type="button"]:hover,
input[type="button"]:active,
input[type="button"]:focus,
input[type="reset"]:hover,
input[type="reset"]:active,
input[type="reset"]:focus,
input[type="submit"]:hover,
input[type="submit"]:active,
input[type="submit"]:focus {
	border-color: #aaa #bbb #bbb;
	box-shadow: inset 0 -1px 0 rgba(255, 255, 255, 0.5), inset 0 2px 5px rgba(0, 0, 0, 0.15);
}

/*--------------------------------------------------------------
# Formattings
--------------------------------------------------------------*/
hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

/*--------------------------------------------------------------
# Lists
--------------------------------------------------------------*/
ul, ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: bold;
}

dd {
	margin: 0 1.5em 1.5em;
}

/*--------------------------------------------------------------
# Tables
--------------------------------------------------------------*/
table {
	margin: 0 auto 1.5em;
	background: #363a3c;
	color: #ffffff;
	font-size: 18px;
	font-size: 1.125rem;
	font-weight: 300;
	padding: 2em;
}

table th, table td {
	padding: 0.2em 1.2em;
}

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
.comment-navigation,
.posts-navigation,
.post-navigation,
.paging-navigation {
	padding: 2em;
	font-family: "Spoqa Han Sans", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	text-align: left;
	overflow: hidden;
	max-width: 62em;
	margin: 0 auto;
	-webkit-transform: skewX(14deg);
	transform: skewX(14deg);
	font-style: italic;
}

.comment-navigation a,
.posts-navigation a,
.post-navigation a,
.paging-navigation a {
	display: block;
	width: 100%;
	text-decoration: none;
	padding: 2em;
	border: 1px solid #cccccc;
	color: #363a3c;
}

.comment-navigation a:hover, .comment-navigation a:focus,
.posts-navigation a:hover,
.posts-navigation a:focus,
.post-navigation a:hover,
.post-navigation a:focus,
.paging-navigation a:hover,
.paging-navigation a:focus {
	box-shadow: 0px 0px 15px #878d91;
	/*border: 1px solid $color__light_gray;*/
	color: #ffffff;
	/*border-color: $color__link;*/
	/* Vivid colors */
	background: #51CF66;
	/* Old browsers */
	background: -moz-linear-gradient(left, rgba(81, 207, 102, 0.8) 0%, rgba(50, 154, 240, 0.8) 100%);
	/* FF3.6-15 */
	background: -webkit-linear-gradient(left, rgba(81, 207, 102, 0.8) 0%, rgba(50, 154, 240, 0.8) 100%);
	/* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to right, rgba(81, 207, 102, 0.8) 0%, rgba(50, 154, 240, 0.8) 100%);
	/* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#66F485', endColorstr='#17ACFE',GradientType=1 );
	/* IE6-9 */
}

.nav-links {
	margin: 0 auto;
	padding: 0 .4em;
}

.nav-previous {
	padding-bottom: 1em;
}

@media screen and (min-width: 62.5rem) {
	.nav-links {
		display: flex;
		justify-content: space-between;
		align-items: stretch;
		padding: 0;
	}
	.comment-navigation .nav-previous,
	.comment-navigation .nav-next,
	.posts-navigation .nav-previous,
	.posts-navigation .nav-next,
	.post-navigation .nav-previous,
	.post-navigation .nav-next {
		float: left;
		display: flex;
		align-items: stretch;
		width: 48%;
	}
	.comment-navigation .nav-next,
	.posts-navigation .nav-next,
	.post-navigation .nav-next {
		float: right;
		text-align: right;
	}
	.nav-previous {
		padding-bottom: 0;
	}
}

.post-navigation {
	margin-bottom: 3em;
}

.post-navigation .meta-nav {
	display: block;
	font-size: 16px;
	font-size: 1rem;
	color: #878d91;
	font-weight: 300;
}

@media screen and (min-width: 30em) {
	.post-navigation {
		padding-bottom: 1em;
	}
}

.post-navigation .post-title {
	font-size: 28px;
	font-size: 1.75rem;
	font-weight: 100;
	color: #1d1f21;
}

.post-navigation a:hover,
.post-navigation a:focus {
	color: #ffffff !important;
}

.pagination {
	font-family: "Spoqa Han Sans", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	background: #f8fafb;
	-webkit-transform: skewX(14deg);
	transform: skewX(14deg);
	font-style: italic;
	margin-top: 4em;
	font-size: 0.8em;
}

.pagination .nav-links {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
}

.pagination a,
.pagination span {
	padding: .5em 1.5em;
	background: white;
	border: 1px solid gainsboro;
	margin: 5px 5px;
}

.pagination a {
	text-decoration: none;
}

.pagination a:focus, .pagination a:hover {
	color: white;
	background: #51CF66;
}

.pagination .current {
	font-weight: 600;
	color: #1d1f21;
}

.pagination .prev .icon {
	margin-right: 1em;
}

.pagination .next .icon {
	margin-left: 1em;
}

/*--------------------------------------------------------------
# Links
--------------------------------------------------------------*/
a {
	color: #51CF66;
	text-decoration: none;
}

a:visited {
	color: rgba(81, 207, 102, 0.8);
}

a:hover {
	border-bottom: 2px solid;
}

a:focus, a:active {
	color: white;
	background-color: #51CF66;
}

a:focus {
	outline: thin dotted;
}

a:focus img {
	outline: thin dotted;
}

a:hover, a:active {
	outline: 0;
}

/*--------------------------------------------------------------
# Layout (Content)
--------------------------------------------------------------*/
html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
	/* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
	box-sizing: inherit;
}

body {
	background: #fff;
	/* Fallback for when there is no custom background color defined. */
}

.row {
	max-width: 75rem;
	margin-left: auto;
	margin-right: auto;
}

.container {
	padding: 0 2rem;
	width: 100%;
	height: inherit;
}

.row::before, .row::after {
	content: ' ';
	display: table;
}

.row::after {
	clear: both;
}

.row.collapse > .column, .row.collapse > .columns {
	padding-left: 0;
	padding-right: 0;
}

