/*--------------------------------------------------------------
# Primary (Main Content)
--------------------------------------------------------------*/
.site-search-overlay {
	transition: all 0.5s;
	width: 0%;
	height: 0%;
	top: 0;
	right: 0;
	z-index: 9;
	/*background: darken( $color__bright_blue_alpha, 30% );*/
	/*@include gradient();*/
	background: rgba(255, 255, 255, 0.7);
	position: fixed;
	/*padding-top: 20px;*/
}

.site-search-overlay .search-form {
	padding: 0 2em;
	text-align: center;
}

.site-search-overlay .search-form .search-field {
	transition: all 0.5s;
	font-size: 40px;
	font-size: 2.5rem;
	font-weight: 300;
	font-family: "Oswald";
	text-align: center;
	/*background: darken( $color__bright_blue_alpha, 30% );*/
	/* Vivid colors */
	background: #51CF66;
	/* Old browsers */
	background: -moz-linear-gradient(left, rgba(81, 207, 102, 0.8) 0%, rgba(50, 154, 240, 0.8) 100%);
	/* FF3.6-15 */
	background: -webkit-linear-gradient(left, rgba(81, 207, 102, 0.8) 0%, rgba(50, 154, 240, 0.8) 100%);
	/* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to right, rgba(81, 207, 102, 0.8) 0%, rgba(50, 154, 240, 0.8) 100%);
	/* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#66F485', endColorstr='#17ACFE',GradientType=1 );
	/* IE6-9 */
	color: #ffffff;
	border: none;
	width: 0%;
	/*                        margin-top: -15px;
                        margin-left: -10px;*/
	border: 1px solid;
	max-width: 50%;
	margin: 0 auto;
}

.site-search-overlay .search-form .search-field:focus, .site-search-overlay .search-form .search-field:active {
	outline: none;
}

.site-search-overlay .search-form .search-field::-webkit-input-placeholder, .site-search-overlay .search-form .search-field::-moz-placeholder, .site-search-overlay .search-form .search-field:-ms-input-placeholder, .site-search-overlay .search-form .search-field:-moz-placeholder {
	color: white;
}

.site-search-overlay .search-form .search-submit {
	display: none;
}

.site-search-overlay .close-search {
	transition: all 1s;
	right: 0;
	top: 0;
	width: 0%;
	height: 0%;
	color: #EFEFF0;
	text-shadow: 0px 0px 2px black;
}

.site-search-overlay.active {
	width: 100%;
	height: 100%;
}

.site-search-overlay.active .search-field {
	width: calc(100vw - 125px);
	margin-top: calc( 50vh - 38px);
}

.site-search-overlay.active .close-search {
	position: absolute;
	right: 2em;
	top: 1em;
}

.blur {
	-webkit-filter: blur(2px);
	filter: blur(2px);
}

