/*--------------------------------------------------------------
## Archives
--------------------------------------------------------------*/
.archive-view .entry-header-container {
	background: #ffffff;
	max-width: 50rem;
	margin: 0 auto;
	padding: 0;
}

.archive-view .entry-header-container .post-thumbnail-container {
	height: 140px;
	width: 200px !important;
	position: absolute;
}

.archive-view .entry-header-container .post-thumbnail-container:before, .archive-view .entry-header-container .post-thumbnail-container:after {
	background: #ffffff;
	height: 140px;
	width: 77px;
}

.archive-view .entry-header-container .post-thumbnail-container .post-thumbnail {
	width: 200px !important;
	height: 140px !important;
}

.archive-view .entry-header {
	margin: 0;
	padding-left: 0px;
	padding-top: 10px;
	border-top: 1px solid gainsboro;
}

.archive-view .entry-header.has-thumbnail {
	padding-left: 220px;
}

.archive-view .entry-title {
	font-size: 20px;
	font-size: 1.25rem;
	margin: 0;
	text-align: left;
}

.archive-view .entry-title a {
	text-decoration: none;
	color: #363a3c;
	line-height: 1.5;
}

.archive-view .entry-meta {
	text-align: left;
	margin-bottom: 0;
}

.archive-view .entry-content {
	max-width: 100% !important;
}

.archive-view .entry-content:after {
	display: none;
}

.archive-view .hentry {
	min-height: 140px;
	margin-bottom: 1.25em;
}

.archive-view .skewed-thumbnail .post-thumbnail {
	-webkit-clip-path: polygon(0 0, 82% 0, 100% 100%, 18% 100%);
	clip-path: polygon(0 0, 82% 0, 100% 100%, 18% 100%);
}

.archive-view .even .skewed-thumbnail {
	/*            -webkit-transform: skewX(-14deg);
            transform: skewX(-14deg)!important;*/
}

.archive-view .even .skewed-thumbnail .post-thumbnail {
	/*                -webkit-transform: skewX(14deg);
                transform: skewX(14deg)!important;*/
	-webkit-clip-path: polygon(18% 0, 100% 0, 82% 100%, 0 100%);
	clip-path: polygon(18% 0, 100% 0, 82% 100%, 0 100%);
}

.archive-view .even .entry-header.has-thumbnail {
	margin-left: 35px;
	padding-left: 185px;
}

.archive-view .sticky {
	margin-bottom: 4em;
}

.archive-view .sticky .entry-header {
	border-top: none;
}

.archive-view .sticky .entry-title {
	font-size: 40px;
	font-size: 2.5rem;
}

.archive-view .sticky .entry-meta {
	margin-bottom: 2em;
}

