.footer-navigation li {
	display: inline-block;
	padding: 0 1em;
}

.footer-navigation:before {
	content: "";
	width: 75%;
	height: 1px;
	display: block;
	border-top: 2px solid #ffffff;
	top: 0;
	z-index: 0;
	margin: 0 auto;
}

.footer-navigation:after {
	content: "";
	width: 50%;
	height: 1px;
	display: block;
	border-bottom: 1px solid #ffffff;
	bottom: 0;
	z-index: 0;
	margin: 0 auto;
}

