/* Ensure center-aligned captions center-align properly. */
.wp-caption.aligncenter {
		margin-left: auto;
		margin-right: auto;
}

/* Give some padding to floated elements, to prevent them butting up against text. */
.wp-caption.alignleft {
		margin-right: 20px;
}

.wp-caption.alignright {
		margin-left: 20px;
}

.wp-caption-text {
	text-align: center;
}

